// 计算深度
export function computeDeep({ row, column }) {
  if (isNaN(row[column.property]) || row[column.property] < 0) {
    return;
  }
  // 收费花深 = 出厂花深 - 剩余花深 - 厂免花深 - 公司照顾花深
  var result =
    row.raw_flowerdeep -
    row.surplus_flowerdeep -
    row.factory_flowerdeep -
    row.care_flowerdeep;
  if (isNaN(result)) {
    return;
  }
  if (result < 0) {
    result = 0
  }
  row.charge_flowerdeep = Number(result).toFixed(2);
  // 磨损费用 = 毫米磨损费 * 收费花深
  var resultPrice = row.mm_wearprice * row.charge_flowerdeep;
  row.wear_price = !isNaN(resultPrice) ? Number(resultPrice).toFixed(2) : 0;
};
// 计算价格
export function computePrice({ row, column }) {
  if (isNaN(row[column.property] || row[column.property] < 0)) {
    return;
  }
  row.inflap_price = (Number(row.intube_price) + Number(row.flap_price)).toFixed(2);
};
