<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>售后管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">公司鉴定</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>新增公司鉴定单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" ref="addFormInline" :model="params" class="demo-form-inline">
        <el-form-item label="销售组织" :rules="rules.company_id" prop="company_id" size="mini">
          <el-select v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1?'optionStyle':''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="客户名称" :rules="rules.customer_name" size="mini" prop="customer_id">
          <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="质检员" :rules="rules.employee_name" prop="inspector_id" size="mini">
          <el-select clearable v-model="params.inspector_id" @focus="EmployeeListInvok" placeholder="请选择" filterable>
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务日期" size="mini" prop="business_time" :rules="rules.business_time">
          <el-date-picker v-model="params.business_time" type="date" placeholder="选择日期" range-separator="-" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="部门" size="mini">
          {{(function(){if(params.department_id == 0)params.department_id = null})()}}
          <el-select clearable v-model="params.department_id" @focus="DepartmentListInvok" placeholder="请选择" filterable @change="params.inspector_id = ''">
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" :disabled="editType === 1" @click=" salesDocumentsVisible = true;" class="addBtn" size="mini">引用单据</el-button>
      <el-button icon="el-icon-circle-plus-outline" type="primary" :disabled="editType === 2" @click="addOrder" class="addBtn" size="mini">增行</el-button>
      <el-button plain icon="el-icon-delete" @click="deletes" size="mini">删行</el-button>
    </div>
    <div class="table_container">
      <xTable ref='xTable' :edit-rules='editRules' :isEdit='isEdit' :tableColumn='tableColumn' />
    </div>

    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack()">取 消</el-button>
      <el-button type="primary" @click="submit('addFormInline')">确 定</el-button>
    </div>
    <companyDalog :message="params" ref="company" @sumbitData='getSumbitData' v-if="salesDocumentsVisible" />
  </div>
</template>

<script>
import companyDalog from "@/components/element/aftersale/share/market_dalog";
import { opthionList } from "common/utils/public_function/commonUtils";
import { tableHead, rowModule } from "./config/company_Add";
export default {
  mixins: [tableHead],
  name: "aftersaleCompanyAdddsdsd",
  data() {
    return {
      authority: {},
      editType: 0, // 操作类型 1 是增行  2是引入
      employeeList: {
        options: []
      },
      salesDocumentsVisible: false, //引用单据表弹框
      loading: false
    };
  },
  components: {
    companyDalog //销售单据
  },
  methods: {
    // 引用返胎入库
    getSumbitData(data) {
      this.editType = 2;
      this.$refs.xTable._proxy(
        "loadData",
        data.map(val => {
          return this.$publicFution.filter(rowModule, val);
        })
      );
    },
    // 增行
    addOrder(step) {
      if (!!this.params.customer_id || step === true) {
        if (step !== true) {
          this.editType = 1;
        }
        this.$refs.xTable._proxy(
          "insertAt",
          this.$publicFution.deepCopy(rowModule),
          -1
        );
      } else {
        this.$message.warning("请先选择客户");
      }
    },
    companyChange() {
      this.$set(this.params, "customer_id", null); // 初始化客户名称下拉选中项
      this.$set(this.params, "inspector_id", null); // 初始化质检员下拉选中项
      this.$set(this.params, "department_id", ""); // 初始化部门下拉选中值
      this.$refs.xTable._proxy("loadData", []);
    },
    goBack() {
      this.$publicFution.goPage("");
      this.$router.push({
        path: "/aftersale/companyjudgement"
      });
    },
    submit() {
      var tableBox = this.$publicFution.deepCopy(
        this.$refs.xTable._proxy("getTableData").fullData
      );
      if (tableBox.length && !tableBox[tableBox.length - 1].goods_name) {
        tableBox.pop();
      }
      if (tableBox.length === 0) {
        this.$message.warning("请完善表格数据");
        return;
      }
      this.$refs.xTable._proxy(
        "loadData",
        this.$publicFution.deepCopy(tableBox)
      );
      this.$refs["addFormInline"].validate(valid => {
        if (valid) {
          this.$refs.xTable._proxy("validate").then(() => {
            var submitData = tableBox.map(val => {
              val.symptom_id = val.symptom_id.join(",");
              return val;
            });
            let obj = {
              customer_id: this.params.customer_id,
              business_time: this.params.business_time,
              company_id: this.params.company_id,
              department_id: this.params.department_id,
              inspector_id: this.params.inspector_id,
              goods_json: JSON.stringify(submitData)
            };
            this.$axios({
              method: "post",
              url: this.$api.addCompanyAppraisal,
              data: obj
            }).then(res => {
              if (res.data.code == 200) {
                this.$message({
                  message: "新增公司鉴定单成功",
                  type: "success"
                });
                this.goBack();
              }
            });
          });
        }
      });
    }
  },
  mounted() {
    this.getNEwlist(); // 获取病象
    let { employee_id, company_admin_id } = localStorage;
    if ([0, 1, 2].includes(Number(employee_id)) || company_admin_id > 0) {
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id),
        business_time: this.$publicFution.formatData(new Date())
      });
    } else {
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id),
        department_id: Number(localStorage.department_id),
        inspector_id: Number(localStorage.employee_id),
        business_time: this.$publicFution.formatData(new Date())
      });
    }
    opthionList(this, localStorage);
    this.addOrder(true);
  }
};
</script>

<style lang="less" scoped>
.table_container {
  /deep/ .longSymptom {
    height: 25px;
    width: 200px;
    .el-select__tags {
      margin-top: -2px;
      .el-tag:first-child {
        .el-select__tags-text {
          display: inline-block;
          width: 80px;
          vertical-align: middle;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
    }
    .el-input__inner {
      height: 24px !important;
      margin-top: -1px;
    }
  }
}
</style>
