<template>
  <div>
    <!--引用销售出库单据-->
    <el-dialog :close-on-click-modal="false" title="引用返胎入库胎号校验信息" :visible="true" class="dialogForm" width="1300px" @close="closeDialog">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <!-- <el-form-item label="客户名称" size="mini" v-if="TableData.tableData[0]">
            <span>{{TableData.tableData[0].customer_name}}</span>
          </el-form-item> -->
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
            </el-date-picker>
          </el-form-item>

          <el-form-item label="客户名称" size="mini">
            <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
              <ul class="customer customer-ul4 customer-ul2">
                <li>客户编号</li>
                <li>客户名称</li>
                <li>联系人</li>
                <li>联系电话</li>
                <li>区域</li>
              </ul>
              <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
                <ul class="customer customer-ul4">
                  <li :title="item.customer_id">{{ item.customer_id }}</li>
                  <li :title="item.customer_name">{{ item.customer_name }}</li>
                  <li :title="item.contact_name">{{item.contact_name}}</li>
                  <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                  <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
                </ul>
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="商品名称" size="mini">
            <el-input v-model="params.goods_name" maxlength="50" class="shortSearchInput"></el-input>
          </el-form-item>

          <el-form-item label="返胎入库单据号" size="mini">
            <el-input v-model="params.instorage_sn" class="shortSearchInput" placeholder="请输入"></el-input>
          </el-form-item>

          <el-form-item label="胎号" size="mini">
            <el-input v-model="params.tire_un" class="shortSearchInput" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" @selection-change="handleSelectionChange" v-loading="loading1" max-height="450">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="instorage_sn" label="返胎入库单据号" min-width="150">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_name" label="客户名称" min-width="130">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="brand_name" label="品牌名称" min-width="150">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500">
            <template slot-scope="scope">
              <pre>{{scope.row.goods_name}}</pre>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号" min-width="130">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_num" label="返胎数量" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="tire_un" label="胎号" min-width="130">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期" min-width="130">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" label="备注" min-width="130">
          </el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog()">取 消</el-button>
        <el-button class="tx" type="primary" @click="dialogSubmitForm()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/sever/api";
import { getData } from "common/utils/public_function/commonUtils";
export default {
  data() {
    return {
      authority: {},
      TableData: {
        tableData: []
      },
      params: {},
      loading: false,
      searchParams: {},
      timeValue: [],
      dialogmultipleSelection: [], //客户列表
      addmultipleSelection: []
    };
  },
  props: ["message"],
  methods: {
    handleSelectionChange(val) {
      this.addmultipleSelection = val;
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      getData(this.searchParams);
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {});
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: {
          customer_id: this.message.customer_id,
          company_id: this.message.company_id
        },
        TableData: this.TableData, //列表数据
        API: "getReturnTireList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
    },
    closeDialog() {
      //引用销售出库单据 弹出框取消
      this.$parent.salesDocumentsVisible = false;
    },
    dialogSubmitForm() {
      let This = this;
      //返回引用的出库单单据数据
      for (var i in this.addmultipleSelection) {
        this.dialogmultipleSelection.push(
          this.addmultipleSelection[i].goods_tire_id
        );
      }
      let addr = this.dialogmultipleSelection;
      this.$axios({
        method: "post",
        url: api.CompanyAppraisalList,
        data: {
          goods_tire_id: addr.toString()
        }
      }).then(res => {
        if (res.data.code == 200) {
          res.data.data.data.forEach((item, i) => {
            let _intube_price = Number(item.intube_price),
              _flap_price = Number(item.flap_price);
            _flap_price = isNaN(_flap_price) ? 0 : _flap_price;
            _intube_price = isNaN(_intube_price) ? 0 : _intube_price;
            // item.inflap_price = Number(
            //   Number(item.intube_price) + Number(item.flap_price)
            // );
            item.inflap_price = _intube_price + _flap_price;
            item.symptom_id = item.symptom_id ? item.symptom_id : [];
          });
          this.$set(this.$parent, "TableData", res.data.data.data);
          this.$emit("sumbitData", res.data.data.data);
          this.$set(this.$parent, "addOrderShow", true);
          this.$set(
            this.$parent.params,
            "customer_id",
            res.data.data.data[0].customer_id
          );
          this.$set(this.$parent, "customerList", [
            {
              customer_id: res.data.data.data[0].customer_id,
              customer_name: res.data.data.data[0].customer_name,
              contact_name: "",
              customer_mobile: "",
              address: ""
            }
          ]);
          this.$parent.salesDocumentsVisible = false;
        }
      });
    }
  },
  mounted() {
    // 赋值 params(当前公司ID)
    this.params.company_id = this.message.company_id;

    this.$set(this, "searchParams", {
      params: {
        customer_id: this.message.customer_id,
        company_id: this.message.company_id
      },
      TableData: this.TableData, //列表数据
      API: "getReturnTireList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams); //获取销售订单列表
  }
};
</script>

